.page {
  font-family: "Poppins", sans-serif;
  font-size: 16px;
  line-height: 100%;

  color: $color-font;

  scroll-behavior: smooth;

  &__body {
    min-width: 320px;
    margin: 0;
  }

  &__menu {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;

    opacity: 0;
    transition: all 0.3s;
    transform: translateX(-100%);
    pointer-events: none;

    &:target {
      opacity: 1;
      transform: translateX(0);
      pointer-events: all;
    }
  }

  &:has(.page__menu:target) {
    overflow: hidden;
  }
}
